'use strict';
/**
 * @api GET_LOGIN() GET_LOGIN
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiDescription 判断是否已登录
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * GET_LOGIN(({isLogin})=>{
 *  console.log(isLogin)
 * })
 * @apiParamExample {string} 返回值
 * {
 *      isLogin: true
 *  }
 **/

/**
 * todo LOGIN
 */
import {WinJSDec, appError, fnError} from './APP/config';

var name = 'IS_LOGIN';
var IS_LOGIN = (fn=_=>{}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('login', 'isLogin', function (res) {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default IS_LOGIN;